.shopCategoryBanner{
    display: block;
    margin: 30px auto;
    width: 82%;
}
.shopCategoryIndexSort{
    display: flex;
    margin:0 205px 0 170px;
    justify-content: space-between;
    align-items:center;
}

.shopCategoryIndexSort p span{
    font-weight: 600;
}

.shopCategorySort{
    padding: 10px 20px;
    border-radius: 40px;
    border: 1px solid #888;
}

.shopCategoryProducts{
    margin: 20px 170px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    row-gap: 80px;
}
.shopCategoryLoadmore{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 150px auto;
    width: 233px;
    height: 69px;
    border-radius: 75px;
    background-color: #ededed;
    color: #787878;
    font-size: 18px;
    font-weight:500;
}

