* {
  margin: 0;
  padding: 0;
  font-family: "Roboto Condensed", sans-serif;
}

.navbar {
  display: flex;
  justify-content: space-around;
  padding: 16px;
  box-shadow: 0 1px 3px -2px black;
}

.nav-logo {
  display: flex;
  align-items: center;
  gap: 10px;
}

.nav-logo p {
  color: #171717;
  font-size: 35px;
  font-weight: 600;
}

.nav-menu {
  display: flex;
  align-items: center;
  list-style: none;
  gap: 50px;
  font-size: 20px;
  font-weight: 500;
}

.nav-menu li {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 3px;
  cursor: pointer;
}
.nav-menu li a {
  font-size: 20px;
  color: #171717;
  text-decoration: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2px;
  cursor: pointer;
}
.nav-menu hr {
  border: none;
  width: 100%;
  height: 3px;
  border-radius: 10px;
  background-color: #b91f1f;
}

.nav-login-cart {
    display: flex;
    align-items: center;
    gap:45px;
}

.nav-login-cart button{
    width: 157px;
    height: 58px;
    outline: none;
    border: 2px solid #2f2e2e;
    border-radius: 75px;
    color: #353434;
    font-size: 20px;
    font-weight: 500;
    background:white;
    cursor: pointer;
}

.nav-login-cart button:active{
    background: #f3f3f3;
}

.nav-cart-count{
    width: 22px;
    height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -35px;
    margin-left: -55px;
    border-radius: 11px;
    font-size: 14px; 
    background: rgb(202, 44, 44);
    color: white;
}